





































































import Vue from 'vue'
import { mapState } from 'vuex'
import SidebarNavigation from './Navigation.vue'
import SidebarFooter from './Footer.vue'
import SidebarTrackingRow from './TrackingRow.vue'
import SidebarTips from './Tips.vue'

export default Vue.extend({
  components: { SidebarTrackingRow, SidebarTips, SidebarNavigation, SidebarFooter },
  data() {
    return {
      activeMetric: 'opens'
    }
  },
  computed: {
    ...mapState(['campaigns']),

    hasCampaigns(): boolean {
      return this.campaigns.length > 0
    },

    totalEmails(): number {
      let total = 0
      for (let i = 0, n = this.campaigns.length; i < n; i++) {
        total += this.campaigns[i].metrics.emails
      }
      return total
    },
    totalEvents(): number {
      let total = 0
      for (let i = 0, n = this.campaigns.length; i < n; i++) {
        total += this.campaigns[i].metrics[this.activeMetric]
      }
      return total
    }
  },

  methods: {
    openModal(): void {
      window._backend
        .withSuccessHandler(function() {
          window._frontend.close()
        })
        .sheetUIShowModal()
    },
    setActiveMetric(event: 'opens' | 'clicks'): void {
      this.activeMetric = event
    }
  }
})
