








































































































































































import Vue from 'vue'
import { mapState } from 'vuex'
import ModalFooterIcons from './FooterIcons.vue'

export default Vue.extend({
  components: { ModalFooterIcons },
  computed: mapState(['user']),
  data: function() {
    return {
      isCancellationInProgress: false,
      isCancellationSuccessful: false,
      cancellationReason: 'none',
      cancellationExplanation: null,
      reasons: ['Project ended', 'Too expensive', 'Not enough use', 'Issues with Mailmeteor', 'Other']
    }
  },
  methods: {
    setLocation(ui: string) {
      this.$store.dispatch('setLocation', ui)
    },
    cancelSubscription: function() {
      this.isCancellationInProgress = true

      window._backend.withSuccessHandler(this.cancelSubscriptionSuccess).sheetUIDataSubscriptionCancel(this.user, this.cancellationReason, this.cancellationExplanation)
    },
    cancelSubscriptionSuccess: function() {
      this.isCancellationSuccessful = true
    },
    close: function() {
      window._frontend.close()
    }
  }
})
