
















































































































































































import Vue from 'vue'
import { mapState } from 'vuex'

import ModalFooterIcons from './FooterIcons.vue'
import ModalSelectorAlias from './SelectorAlias.vue'
import ModalSelectorTemplates from './SelectorTemplates.vue'

export default Vue.extend({
  name: 'ModalHome',
  components: {
    ModalFooterIcons,
    ModalSelectorAlias,
    ModalSelectorTemplates
  },

  computed: {
    ...mapState(['location', 'sheet', 'user', 'templates', 'merge']),
    mergeTracking: {
      get(): boolean {
        return this.merge.tracking
      },
      set(newValue: boolean): void {
        this.$store.dispatch('merge/setTracking', newValue)
      }
    },

    isOnboarding(): boolean {
      const location = this.location as string
      return location.indexOf('onboarding') > 0
    },

    isActionsDisabled(): boolean {
      if (this.user.blocked) {
        return true
      } else if (this.isQuotaExceeded) {
        return true
      } else if (!this.$store.getters.getActiveTemplate) {
        return true
      } else if (this.sheet.emailing.columnHeading === '') {
        return true
      }

      return false
    },

    /**
     * Quota
     */

    isQuotaInsufficient(): boolean {
      if (!this.user.quota || !this.user.quota.limit || this.isQuotaExceeded) {
        return false
      }

      return this.sheet.emailing.recipients > this.user.quota.available
    },

    isQuotaUpgradable(): boolean {
      return this.user.quota.limit <= 200
    },

    isQuotaExceeded(): boolean {
      return this.user.quota.available <= 0
    },

    maxUserQuota(): number {
      const domain = this.user.domain
      const gmaildomains = ['gmail.com', 'googlemail.com']
      return gmaildomains.indexOf(domain) >= 0 ? 500 : 1500
    },

    maxEmailsToSend(): number {
      if (this.isQuotaExceeded) {
        return this.sheet.emailing.recipients
      }

      return this.user.quota.available > this.sheet.emailing.recipients ? this.sheet.emailing.recipients : this.user.quota.available
    }
  },

  methods: {
    setLocation(ui: string): void {
      this.$store.dispatch('setLocation', ui)
    },
    showUIAlias(): void {
      if (this.user && this.user.premium) {
        this.setLocation('/alias')
        return
      }

      this.$store.dispatch('showSplashScreen', 'alias')
    },
    showUIPreview(): void {
      this.setLocation('/preview')
    },
    showUISending(): void {
      this.setLocation('/sending')
    }
  }
})
