import Vue from 'vue'
import Vuex from 'vuex'
import { user, sheet, aliases, templates, merge, campaigns } from './modules'

Vue.use(Vuex)

export default new Vuex.Store({
  /**
   * Modules
   */

  modules: {
    user,
    sheet,
    merge,
    aliases,
    templates,
    campaigns
  },

  /**
   * State
   */

  state: {
    user: user.state,
    templates: templates.state,
    merge: merge.state,
    sheet: sheet.state,
    aliases: aliases.state,
    campaigns: campaigns.state,

    // Routing
    location: '',
    navigation: [] as string[],

    // Overlays
    splash: { active: false, theme: 'upgrade' },
    butterbar: { active: false, theme: 'error', message: '' },

    // Application status
    mode: (window._app && window._app.mode) || 'modal',
    ready: false,

    // Sidebar only
    activeCampaign: null
  },

  /**
   * Getters
   */

  getters: {
    getActiveTemplate(state): Types.Template | null {
      const mergeTemplate = state.merge.template
      const templates = state.templates

      if (!mergeTemplate) {
        return null
      }

      for (let i = 0, n = templates.length, id = mergeTemplate.id; i < n; i++) {
        if (templates[i].id === id) {
          return templates[i]
        }
      }

      return null
    }
  },
  /**
   * Actions
   */

  actions: {
    /**
     * Navigation
     */

    setLocation(context, newValue: string) {
      if (newValue === ':back') {
        context.state.navigation.pop()
        const last = context.state.navigation.pop()
        newValue = last || '/'
      }

      context.state.navigation.push(newValue)
      context.state.location = newValue
    },

    /**
     * Splash screen
     */

    showSplashScreen(context, newValue: string) {
      context.state.splash.active = true
      context.state.splash.theme = newValue
    },
    closeSplashScreen(context) {
      context.state.splash.active = false
    },

    /**
     * Butterbar
     */

    showButterbar(context, newValue?: string | { theme: 'error' | 'warning'; message: string }) {
      context.state.butterbar.active = true

      if (typeof newValue === 'string') {
        context.state.butterbar.theme = 'error'
        context.state.butterbar.message = newValue
      } else if (newValue) {
        context.state.butterbar.theme = newValue.theme
        context.state.butterbar.message = newValue.message
      }
    },
    closeButterbar(context) {
      context.state.butterbar.active = false
      context.state.butterbar.message = ''
      context.state.butterbar.theme = 'error'
    },

    ready(context) {
      context.commit('ready')
    },

    setUserNotificationStatus(context, newValue: boolean) {
      if (!newValue) {
        window._backend.sheetUIMarkNotificationsAsRead()
      }

      context.state.user.notificationsUnread = newValue
    },

    /**
     * Sidebar
     */

    setActiveCampaign(context, activeCampaign: Types.Campaign | null) {
      context.state.activeCampaign = activeCampaign
    }
  },

  /**
   * Mutations
   */

  mutations: {
    ready(state) {
      state.ready = true
    }
  }
})
