import Vue from 'vue'
import firebase from 'firebase/app'
import App from './App.vue'
import store from './store'
import i18n from './i18n'
import Types from './types'

new Vue({
  store,
  i18n,
  created: function() {
    /**
     * Initialize firebase
     */

    firebase.initializeApp(window._app && window._app.firebase)

    /**
     * Fetch user data from Google Apps Script
     */

    const $store = this.$store
    const mode = window._app && window._app.mode

    if (mode === 'sidebar') {
      window._backend
        // TODO - Handle "withFailureHandler" to send sidebar-errors to bugsnag
        .withSuccessHandler((data: { user: Types.UserProfile; campaigns: Types.Campaign[] }) => {
          // Initialize store's modules
          $store.dispatch('user/initialize', data.user)
          $store.dispatch('campaigns/initialize', data.campaigns)

          // Handle app's location
          $store.dispatch('setLocation', (window._app && window._app.location) || '/sidebar')

          // Set the app is ready (initialized)
          window.setTimeout(function() {
            $store.dispatch('ready', true)
          }, 300)
        })
        .sheetUIDataSidebar()
    } else {
      window._backend
        .withSuccessHandler((data: { user: Types.UserProfile; sheet: Types.Sheet }) => {
          const user = data.user
          const sheet = data.sheet

          // Initialize store's modules
          $store.dispatch('user/initialize', user)
          $store.dispatch('sheet/initialize', sheet)
          $store.dispatch('aliases/initialize', (user && user.aliases) || [])
          $store.dispatch('merge/initialize', user)

          // Handle app's location ("/" by default)
          if (!user.installedAt) {
            $store.dispatch('setLocation', '/onboarding')
          } else {
            $store.dispatch('setLocation', (window._app && window._app.location) || '/')
          }

          if (!user.premium && user.mergesCounter > 0) {
            const mergesCounter = user.mergesCounter
            if (mergesCounter === 2 || mergesCounter === 4) {
              // Propose to add a review at 2 and 4 merges
              // (if user hasn't been doubled yet)
              if (user.quota.limit < 200) {
                $store.dispatch('showSplashScreen', 'add-review')
              } else {
                $store.dispatch('showSplashScreen', 'discount')
              }
            } else if (mergesCounter > 4 && mergesCounter % 3 === 0) {
              // ... or every 3 merges starting at 6 (so at 6, 9, 12, etc.)
              $store.dispatch('showSplashScreen', 'discount')
            }
          }

          // Set the app is ready (initialized)
          window.setTimeout(function() {
            $store.dispatch('ready', true)
          }, 200)

          /* eslint-disable-next-line @typescript-eslint/camelcase */
          window.gtag('event', 'user_event', { user_account: user.premium ? 'Paid' : 'Free' })
        })
        .withFailureHandler((error: Error) => {
          // TODO - Auto-handle "withFailureHandler" to send errors to bugsnag
          $store.dispatch('showButterbar', error.message) // TODO Handle error properly (especially switch account)
          window._logger(error)
        })
        .sheetUIDataModal()
    }
  },

  render: h => h(App)
}).$mount('#app')
