

















































import Vue from 'vue'

export default Vue.extend({
  props: ['file'],
  computed: {
    fileSize(): string {
      return Math.ceil((this.file.size || 0) / 1000) + 'K'
    },
    fileDirectlink(): string {
      return (window._app && window._app.attachments.BASE_URL + [this.$store.state.user.uid, this.file.id].join('/')) || ''
    },
    fileError(): string {
      const err = this.file.error
      return typeof err === 'string' ? err : 'Upload blocked!'
    }
  },
  methods: {
    remove(): void {
      this.$emit('attachment-remove', this.file)
    }
  }
})
