

















import Vue from 'vue'

export default Vue.extend({
  props: ['campaign', 'eventType'],
  computed: {
    percent(): number {
      if (isNaN(this.totalEmails) || this.totalEmails < 1) {
        return 0
      } else if (isNaN(this.totalEvents) || this.totalEvents < 1) {
        return 0
      } else if (this.totalEvents > this.totalEmails) {
        return 100
      } else {
        return Math.round((this.totalEvents / this.totalEmails) * 100)
      }
    },
    totalEmails(): number {
      return this.campaign.metrics.emails
    },
    totalEvents(): number {
      return this.campaign.metrics[this.eventType]
    },
    progressBarWidth(): string {
      return 'width:' + this.percent + '%'
    }
  },
  methods: {
    setActiveCampaign(): void {
      this.$store.dispatch('setActiveCampaign', this.campaign)
      this.$store.dispatch('setLocation', '/sidebar/campaigns')
    }
  }
})
