





























import Vue from 'vue'

export default Vue.extend({
  computed: {
    notificationsUnread: {
      get() {
        return this.$store.state.user.notificationsUnread
      },
      set(newValue: boolean) {
        this.$store.dispatch('setUserNotificationStatus', newValue)
      }
    },
    currentUI: {
      get() {
        return this.$store.state.location
      },
      set(newValue: string) {
        this.$store.dispatch('setLocation', newValue)
      }
    }
  },
  methods: {
    clickProfile: function() {
      if (this.currentUI === '/account') {
        this.currentUI = '/'
        return
      }

      this.currentUI = '/account'
    },
    clickNotification: function() {
      if (this.notificationsUnread) {
        this.notificationsUnread = false
      }

      if (this.currentUI === '/notifications') {
        this.currentUI = '/'
        return
      }

      this.currentUI = '/notifications'
    }
  }
})
