























































































import Vue, { PropType } from 'vue'
import Types from '../../types'

export default Vue.extend({
  props: {
    alias: Object as PropType<Types.Alias>,
    index: Number
  },
  data: function() {
    return {
      name: this.alias.name as string,
      email: this.alias.email as string,
      edit: (this.alias.edit || false) as boolean
    }
  },
  computed: {
    isPrimary(): boolean {
      return this.index === 0
    },
    isEditable(): boolean {
      return !this.isPrimary
    },
    isNewAlias(): boolean {
      return this.alias.edit && !this.alias.name && !this.alias.email ? true : false
    }
  },
  methods: {
    editAlias: function() {
      this.edit = true
    },
    selectAlias: function() {
      if (!this.edit) {
        this.$store.dispatch('aliases/select', this.alias)
        this.$store.dispatch('setLocation', '/')
      }
    },
    updateAlias: function() {
      if (/@/.test(this.email) && this.name) {
        this.edit = false
        this.$store.dispatch('aliases/update', { oldAlias: this.alias, newAlias: { name: this.name, email: this.email } })
      }

      if (this.isNewAlias) {
        this.$emit('saveNewAlias')
      }
    },
    deleteAlias: function() {
      this.$store.dispatch('aliases/remove', this.alias)
    }
  }
})
