import Vue from 'vue'
import { ActionTree, MutationTree } from 'vuex'
import Types from '../../types'

const state = {
  emailing: {
    column: 2,
    columnName: 'Email',
    columnHeading: 'B',
    recipients: 10
  },
  stats: {
    startRow: 1,
    startColumn: 1,
    lastRow: 10,
    numRows: 10,
    numColumns: 10
  },
  headers: [],
  name: '',
  provider: '',
  sheetId: '',
  spreadsheetId: '',
  activeSheet: '', // It's basically `spreadsheetId/sheetId`
  isEmpty: false,
  isFiltered: false
}

const actions: ActionTree<Types.Sheet, Types.RootState> = {
  /**
   * Initialize
   */

  initialize(context, sheet: Types.Sheet) {
    context.commit('set', sheet)
  }
}

const mutations: MutationTree<Types.Sheet> = {
  set(state, sheet: Types.Sheet) {
    state.isEmpty = sheet.isEmpty
    state.isFiltered = sheet.isFiltered
    state.activeSheet = sheet.activeSheet
    state.sheetId = sheet.sheetId
    state.spreadsheetId = sheet.spreadsheetId
    state.name = sheet.name
    state.provider = sheet.provider

    Vue.set(state, 'emailing', sheet.emailing)
    Vue.set(state, 'stats', sheet.stats)
    Vue.set(state, 'headers', sheet.headers)
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations
}
