










import Vue from 'vue'
import { mapState } from 'vuex'
import AppRouter from './components/Router.vue'
import ModalSplashScreen from './components/Modal/SplashScreen.vue'
import ModalButterBar from './components/Modal/ButterBar.vue'

export default Vue.extend({
  name: 'mailmeteor',
  components: {
    AppRouter,
    ModalSplashScreen,
    ModalButterBar
  },
  data() {
    return { unloaded: false }
  },
  computed: mapState(['ready', 'mode']),
  watch: {
    ready(oldValue: string, newValue: string) {
      const delay = this.mode === 'sidebar' ? 1200 : 800
      setTimeout(() => {
        const loader = document.getElementById('UILoader')
        const parent = loader && loader.parentNode
        if (loader && parent) {
          parent.removeChild(loader)
        }

        this.unloaded = true
      }, delay)
    }
  }
})
