





import Vue from 'vue'
import { mapState } from 'vuex'
import ModalHome from './Modal/Home.vue'
import ModalAccount from './Modal/Account.vue'
import ModalAccountCancel from './Modal/AccountCancel.vue'
import ModalOnboardingWelcome from './Modal/OnboardingWelcome.vue'
import ModalOnboardingVideo from './Modal/OnboardingVideo.vue'
import ModalNotifications from './Modal/Notifications.vue'
import ModalCompose from './Modal/Compose.vue'
import ModalTemplates from './Modal/Templates.vue'
import ModalAliases from './Modal/Aliases.vue'
import ModalPreview from './Modal/Preview.vue'
import ModalSending from './Modal/Sending.vue'
import ModalSent from './Modal/Sent.vue'
import SidebarHome from './Sidebar/Home.vue'
import SidebarCampaign from './Sidebar/Campaign.vue'

const routes = {
  '/': ModalHome,
  '/account': ModalAccount,
  '/account/cancel': ModalAccountCancel,
  '/onboarding': ModalOnboardingWelcome,
  '/onboarding/video': ModalOnboardingVideo,
  '/notifications': ModalNotifications,
  '/alias': ModalAliases,
  '/templates': ModalTemplates,
  '/templates/new': ModalCompose,
  '/templates/edit': ModalCompose,
  '/preview': ModalPreview,
  '/sending': ModalSending,
  '/sent': ModalSent,
  '/sidebar': SidebarHome,
  '/sidebar/campaigns': SidebarCampaign
}

const Router = Vue.extend({
  mounted: function() {
    // eslint-disable-next-line @typescript-eslint/camelcase
    window.gtag('event', 'user_event', { user_account: 'free' })
  },
  computed: {
    ...mapState(['location']),
    routedComponent() {
      const location = this.location as string
      const path = location.split('?')[0]
      // eslint-disable-next-line @typescript-eslint/camelcase
      window.gtag('config', 'UA-136395985-1', { page_path: location })

      // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
      // @ts-ignore
      return routes[path]
    }
  }
})

export default Router
