
























import Vue from 'vue'

export default Vue.extend({
  data: function() {
    return { loading: false }
  },
  methods: {
    startOnboarding: function() {
      if (this.loading) {
        return
      }

      this.loading = true

      const template = {
        subject: this.$t('SHEET_ONBOARDING_TEMPLATE_SUBJECT'),
        htmlBody: this.$t('SHEET_ONBOARDING_TEMPLATE_HTMLBODY')
      }

      this.$store.dispatch('templates/create', template)
      window._backend.sheetUIStartOnboarding()
    },
    skipOnboarding: function() {
      this.$store.dispatch('setLocation', '/')

      window.saasmetrics('event', {
        category: 'engagement',
        action: 'skip_onboarding',
        label: 'Skipped at step 2'
      })
    }
  }
})
