








































import Vue from 'vue'

export default Vue.extend({
  computed: {
    active(): boolean {
      return this.$store.state.butterbar.active
    },
    theme(): string {
      return this.$store.state.butterbar.message
    },
    message(): string {
      let errorMessage = this.$store.state.butterbar.message

      if (errorMessage.substr(0, 7) === 'Error: ') {
        errorMessage = errorMessage.substr(7)
      }

      return errorMessage || this.$i18n.t('ERROR_UNHANDLED')
    }
  },
  methods: {
    close: function() {
      this.$store.dispatch('closeButterbar')
    }
  }
})
