




















































import Vue from 'vue'
import * as firebase from 'firebase/app'
import 'firebase/storage'
import ModalComposeAttachments from './ComposeAttachments.vue'
import * as Types from '../../types'

export default Vue.extend({
  components: { ModalComposeAttachments },
  props: ['templateHtmlBody', 'templateAttachments'],
  data: function() {
    return {
      sheetUIEditor: null as any // eslint-disable-line
    }
  },
  watch: {
    templateHtmlBody: function() {
      this.populateEditorContent()
    }
  },
  mounted: function() {
    this.makeToolbar()

    const element = document.getElementById('sheetUIEditorAttachmentsHandler')

    if (element) {
      element.addEventListener('change', this.onUploadingFiles, false)
    }
  },
  methods: {
    makeToolbar: function() {
      // Initialize Google API
      const goog = window.goog as any // eslint-disable-line @typescript-eslint/no-explicit-any

      if (!goog || !goog.editor) {
        throw new Error('Unable to initialize Google API / Google Editor.')
      }

      // Create an editable field.
      this.sheetUIEditor = new goog.editor.SeamlessField('sheetUIEditorEditableContainer')

      // Create and register all of the editing plugins you want to use.
      this.sheetUIEditor.registerPlugin(new goog.editor.plugins.BasicTextFormatter())
      this.sheetUIEditor.registerPlugin(new goog.editor.plugins.RemoveFormatting())
      this.sheetUIEditor.registerPlugin(new goog.editor.plugins.ListTabHandler())
      this.sheetUIEditor.registerPlugin(new goog.editor.plugins.SpacesTabHandler())
      this.sheetUIEditor.registerPlugin(new goog.editor.plugins.EnterHandler())
      this.sheetUIEditor.registerPlugin(new goog.editor.plugins.HeaderFormatter())
      this.sheetUIEditor.registerPlugin(new goog.editor.plugins.LinkDialogPlugin())
      this.sheetUIEditor.registerPlugin(new goog.editor.plugins.LinkBubble())

      const attachFileButton = goog.ui.editor.ToolbarFactory.makeButton('goog-attach-file', 'Attach file', '', 'tr-icon tr-attach-file')

      // Specify the buttons to add to the toolbar
      const buttons = [
        goog.editor.Command.BOLD,
        goog.editor.Command.ITALIC,
        goog.editor.Command.UNDERLINE,
        goog.editor.Command.REMOVE_FORMAT,
        goog.editor.Command.FONT_COLOR,
        goog.editor.Command.LINK,
        attachFileButton,
        goog.editor.Command.JUSTIFY_LEFT,
        goog.editor.Command.JUSTIFY_CENTER,
        goog.editor.Command.JUSTIFY_RIGHT,
        goog.editor.Command.UNORDERED_LIST,
        goog.editor.Command.ORDERED_LIST,
        goog.editor.Command.OUTDENT,
        goog.editor.Command.INDENT
      ]

      const myToolbar = goog.ui.editor.DefaultToolbar.makeToolbar(buttons, goog.dom.getElement('sheetUIEditorToolbar'))
      new goog.ui.editor.ToolbarController(this.sheetUIEditor, myToolbar)
      this.sheetUIEditor.makeEditable()

      goog.events.listen(this.sheetUIEditor, goog.editor.Field.EventType.DELAYEDCHANGE, this.onUpdateEditorContent)
      goog.events.listen(attachFileButton, goog.ui.Component.EventType.ACTION, this.onAttachingFile)
    },
    onUpdateEditorContent: function() {
      this.$emit('content-update', this.sheetUIEditor.getCleanContents())
    },
    populateEditorContent: function() {
      const goog = window.goog as any // eslint-disable-line
      const htmlContent = this.templateHtmlBody
      if (htmlContent) {
        const sanitizer = new goog.html.sanitizer.HtmlSanitizer.Builder()
          .allowCssStyles()
          .inlineStyleRules()
          .withCustomNetworkRequestUrlPolicy(goog.html.SafeUrl.sanitize)
          .allowFormTag()
          .build()
        const innerHTML = sanitizer.sanitize(htmlContent)
        this.sheetUIEditor.setSafeHtml(false, innerHTML)
      }
    },
    onAttachingFile: function() {
      if (!this.$store.state.user.premium) {
        this.$emit('attachment-upgrade')
        return
      }

      const element = document.getElementById('sheetUIEditorAttachmentsHandler')

      if (element) {
        element.click()
      }
    },
    onUploadingFiles: function(e: Event) {
      if (!e || !e.target) {
        return
      }

      const target = e.target as HTMLInputElement & { files: Array<string> }

      const files = target.files
      const attachmentsTotalSize = this.getTemplateAttachmentsTotalSize()
      const attachmentsLength = this.getTemplateAttachmentsLength()

      for (let i = 0, n = files.length; i < n; i++) {
        const file = files[i]

        if (!file.size) {
          continue
        }

        this.handleFileUpload((file as unknown) as Types.AttachmentUpload, attachmentsTotalSize, attachmentsLength + i + 1)
      }

      target.value = '' // null
    },
    handleFileUpload: function(file: Types.AttachmentUpload, attachmentsTotalSize: number, attachmentsLength: number) {
      const ATTACHMENTS_CONFIG = (window._app && window._app.attachments) || {}
      const ATTACHMENTS_MAX_FILES = ATTACHMENTS_CONFIG.MAX_FILES || 5
      const ATTACHMENTS_MAX_FILESIZE = (ATTACHMENTS_CONFIG.MAX_FILESIZE * 3) / 4 || 4 * 1024 * 1024
      const ATTACHMENTS_MAX_SIZE = ATTACHMENTS_MAX_FILESIZE

      // Register file
      file.id = this.generateFileId()
      this.$emit('attachment-create', { id: file.id, size: file.size, name: file.name, progress: 0 })

      // Handle file upload progress
      const updateFile = (key: string, value: string | number | boolean | null) => {
        this.$emit('attachment-update', { id: file.id, key: key, value: value })
      }

      // File validation
      if (file.size > ATTACHMENTS_MAX_FILESIZE) {
        updateFile('error', 'File too large (max: 4Mo)')
        return
      }

      if (attachmentsLength > ATTACHMENTS_MAX_FILES) {
        updateFile('error', 'Too many attachments (max: 10 files)')
        return
      }

      if (attachmentsTotalSize + file.size > ATTACHMENTS_MAX_SIZE) {
        updateFile('error', 'Attachments too large (max: 4Mo)')
        return
      }

      if (this.isFileExtensionBlocked(file)) {
        updateFile('error', 'Blocked for security reasons!')
        return
      }

      // Upload file
      const storagePath = '/attachments/' + this.$store.state.user.uid + '/' + file.id + '/' + file.name
      const storageRef = firebase.storage().ref(storagePath)
      const uploadTask = storageRef.put(file)

      uploadTask.on(
        'state_changed',
        function progress(snapshot) {
          const progress = Math.round((snapshot.bytesTransferred / snapshot.totalBytes) * 100)
          updateFile('progress', progress)
        },
        function error(err: Error) {
          window._logger(err)
          updateFile('error', err.message)
        },
        function complete() {
          window.setTimeout(function() {
            updateFile('progress', null)
            updateFile('uploaded', true)
          }, 800)

          window.gtag('send', 'event', 'Templates', 'upload-attachment')
          window.saasmetrics('event', { category: 'attachment', action: 'upload', value: file.size })
        }
      )
    },
    onAttachmentRemoval(attachment: Types.AttachmentEvent) {
      this.$emit('attachment-remove', attachment)
    },
    generateFileId: function() {
      const PUSH_CHARS = '-0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZ_abcdefghijklmnopqrstuvwxyz'
      let lastPushTime = 0
      const lastRandChars = []

      let now = new Date().getTime()
      const duplicateTime = now === lastPushTime
      lastPushTime = now

      const timeStampChars = new Array(8)
      let i = 0
      for (i = 7; i >= 0; i--) {
        timeStampChars[i] = PUSH_CHARS.charAt(now % 64)
        now = Math.floor(now / 64)
      }
      if (now !== 0) throw new Error('We should have converted the entire timestamp.')

      let id = timeStampChars.join('')

      if (!duplicateTime) {
        for (i = 0; i < 12; i++) {
          lastRandChars[i] = Math.floor(Math.random() * 64)
        }
      } else {
        for (i = 11; i >= 0 && lastRandChars[i] === 63; i--) {
          lastRandChars[i] = 0
        }
        lastRandChars[i]++
      }
      for (i = 0; i < 12; i++) {
        id += PUSH_CHARS.charAt(lastRandChars[i])
      }
      if (id.length != 20) throw new Error('Length should be 20.')

      return id
    },
    getTemplateAttachmentsTotalSize: function() {
      let size = 0
      for (const id in this.templateAttachments) {
        const attachment = this.templateAttachments[id]
        size += attachment.error ? 0 : attachment.size
      }
      return size
    },
    getTemplateAttachmentsLength: function() {
      let i = 0
      for (const id in this.templateAttachments) {
        const attachment = this.templateAttachments[id]
        i += attachment.error ? 0 : 1
      }
      return i
    },

    isFileExtensionBlocked(file: { name: string }) {
      // prettier-ignore
      const extensions = ['ade','adp','apk','appx','appxbundle','bat','cab','chm',
        'cmd','com','cpl','dll','dmg','exe','hta','ins','isp','iso','jar','js',
        'jse','lib','lnk','mde','msc','msi','msix','msixbundle','msp','mst','nsh',
        'pif','ps1','scr','sct','shb','sys','vb','vbe','vbs','vxd','wsc','wsf','wsh']

      const fileName = (file && file.name) || ''
      const fileExtension = fileName.split('.').pop()

      return fileExtension && extensions.indexOf(fileExtension) >= 0
    }
  }
})
