
import Vue from 'vue'

export default Vue.extend({
  props: ['templatedBody', 'templateAttachments'],
  watch: {
    templatedBody: function() {
      const $el = this.$el as HTMLIFrameElement

      if (!$el || !$el.contentWindow) {
        return
      }

      $el.contentWindow.document.open()
      $el.contentWindow.document.write(this.stylizedIframe)
      $el.contentWindow.document.close()
    }
  },
  computed: {
    stylizedIframe(): string {
      const iframeCSS =
        '<style>html, body { font-family: Arial, Helvetica, sans-serif;border: 0;display: block;height: 100%;' +
        'margin: 0;padding: 0;width: 100%;white-space: pre-wrap;font-size: 13px;line-height: 19.5px;}' +
        '.addon-preview-body {min-height: 145px;}' +
        '.addon-preview-attachments {margin-top: 18px; max-width: 80%; font-weight: bold;}' +
        '.addon-preview-attachments > div {margin-bottom: 9px; background-color: #f5f5f5;padding: 4px 4px 4px 8px;}' +
        '.addon-preview-attachments > div > .addon-preview-filename {display: inline-block; color: #15c;vertical-align:middle;' +
        'text-decoration: none;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;max-width: 260px;}' +
        '.addon-preview-attachments > div > .addon-preview-filesize {vertical-align:middle; padding-left: 8px; display: inline-block; color: #777;}' +
        '</' +
        'style>'

      const iframeJS =
        "<script>var links = document.getElementsByTagName('a');" + "for (var i = 0; i < links.length; i++) { links[i].setAttribute('target', '_blank'); };" + '</' + 'script>'
      return iframeCSS + '<div class="addon-preview-body">' + this.templatedBody + '</div>' + this.stylizedAttachments + iframeJS
    },
    stylizedAttachments(): string {
      let html = ''

      for (const id in this.templateAttachments) {
        const attachment = this.templateAttachments[id]

        let link = (window._app && window._app.attachments.BASE_URL) || ''
        link += [this.$store.state.user.uid, attachment.id].join('/')

        html += '<div><a href="' + link + '" class="addon-preview-filename">' + attachment.name + '</a>'
        html += '<span class="addon-preview-filesize">' + Math.ceil((attachment.size || 0) / 1000) + 'K</span></div>'
      }

      return '<div class="addon-preview-attachments">' + html + '</div>'
    }
  },
  render: function(createElement) {
    return createElement('iframe')
  }
})
