import { ActionTree, MutationTree } from 'vuex'
import * as firebase from 'firebase/app'
import 'firebase/database'
import Types from '../../types'

const state = {
  tracking: true,
  template: null,
  senderName: '',
  senderEmail: '',
  alias: null
}

const actions: ActionTree<Types.Merge, Types.RootState> = {
  /**
   * Initializer
   */

  initialize(context, user: Types.UserProfile) {
    context.commit('senderName', user.name)
    context.commit('senderEmail', user.email)
  },

  /**
   * Set the active template (for internal reference)
   *
   * DO NOT CALL THIS FUNCTION TO SELECT THE ACTIVE TEMPLATE,
   * USE "templates/select" INSTEAD WHICH VALIDATES THE TEMPLATE CONTENT.
   */

  setTemplate(context, template: Types.Template | null) {
    context.commit('template', template)
  },

  unsetTemplate(context, template: Types.Template | null) {
    if (!template) {
      context.commit('template', null)
      return
    }

    const activeTemplate = context.state.template
    if (activeTemplate && activeTemplate.id === template.id) {
      context.commit('template', null)
    }
  },

  /**
   * Active alias
   */

  setAlias(context, alias: Types.Alias) {
    context.commit('alias', alias)
    context.commit('senderName', alias.name)
    context.commit('senderEmail', alias.email)
  },

  unsetAlias(context, alias: Types.Alias | null) {
    if (!alias) {
      context.commit('alias', null)
      return
    }

    const activeAlias = context.state.alias
    if (activeAlias && activeAlias.name === alias.name && activeAlias.email === alias.email) {
      context.commit('alias', null)
    }
  },

  /**
   * Do not track?
   */

  setTracking(context, status: boolean) {
    context.commit('tracking', status)
  },

  /**
   * Sender's name & email
   */

  setSenderName(context, name: string) {
    // Update the store
    context.commit('senderName', name)

    // Also updating the backend for later use
    window._backend.sheetUISaveUserName(name)
  },

  setSenderEmail(context, email: string) {
    context.commit('senderEmail', email)
  }
}

const mutations: MutationTree<Types.Merge> = {
  /**
   * Set the active template for the merge
   */

  template(state, template: Types.Template) {
    state.template = template
  },

  /**
   * Set the active alias for the merge
   */

  alias(state, alias: Types.Alias) {
    state.alias = alias
  },

  /**
   * Set the status of the tracking for the merge
   */

  tracking(state, status: boolean) {
    state.tracking = status
  },

  /**
   * Set the senderName / senderEmail of the merge
   */

  senderName(state, senderName: string) {
    state.senderName = senderName
  },

  senderEmail(state, senderEmail: string) {
    state.senderEmail = senderEmail
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations
}
