





























import Vue from 'vue'

export default Vue.extend({
  methods: {
    setLocation(ui: string) {
      this.$store.dispatch('setLocation', ui)
      window._backend.sheetUISetInstallationDate()
    },
    startOnboarding() {
      this.setLocation('/onboarding/video')

      window.saasmetrics('event', {
        category: 'engagement',
        action: 'start_onboarding'
      })
    },
    skipOnboarding() {
      this.setLocation('/')

      window.saasmetrics('event', {
        category: 'engagement',
        action: 'skip_onboarding',
        label: 'Skipped at step 1'
      })
    }
  }
})
