
























































































































































































import Vue from 'vue'
import { mapState } from 'vuex'
import ModalFooterIcons from './FooterIcons.vue'

type Subscription = {
  status: string | null
  end: number | null
}

export default Vue.extend({
  components: { ModalFooterIcons },
  data() {
    return {
      subscription: {
        status: null,
        end: null
      } as Subscription
    }
  },

  computed: {
    ...mapState(['user']),
    quotaRenewal(): string {
      const user = this.user as Types.UserProfile
      const refreshDate = new Date(user.quota.refreshDate)

      if (typeof refreshDate.toLocaleString !== 'function') {
        return refreshDate.toString()
      }

      const now = new Date()
      const time = refreshDate.toLocaleString(undefined, { hour: '2-digit', minute: '2-digit' })
      const refreshToday = now.getDay() === refreshDate.getDay()

      return (refreshToday ? 'Today' : 'Tomorrow') + ' at ' + time
    },
    subscriptionName(): string {
      const user = this.user as Types.UserProfile
      return user.premium ? 'Mailmeteor Premium' : 'Mailmeteor Free'
    },
    subscriptionEnd(): string {
      if (this.subscription.end) {
        const endDate = new Date()
        endDate.setTime(this.subscription.end * 1000)

        if (typeof endDate.toLocaleString !== 'function') {
          return endDate.toString()
        }

        return endDate.toLocaleString(undefined, { year: 'numeric', month: 'numeric', day: 'numeric' })
      }

      return ''
    }
  },
  mounted: function() {
    if (this.user.premium && this.user.subscription) {
      this.fetchSubscriptionDetails()
    }
  },
  methods: {
    setLocation(ui: string) {
      this.$store.dispatch('setLocation', ui)
    },
    cancel() {
      this.setLocation('/')
    },
    cancelAccount() {
      this.$store.dispatch('showSplashScreen', 'cancel')
    },
    fetchSubscriptionDetails() {
      window._backend.withSuccessHandler(this.fetchSubscriptionDetailsSuccess).sheetUIDataSubscription(this.user.subscription)
    },
    fetchSubscriptionDetailsSuccess(data: { subscription: { status: string; current_period_end: number } }) {
      this.$set(this.subscription, 'status', data.subscription.status)
      this.$set(this.subscription, 'end', data.subscription.current_period_end)
    }
  }
})
