






































import Vue from 'vue'
import { mapState } from 'vuex'

export default Vue.extend({
  computed: mapState(['merge', 'sheet']),

  methods: {
    openTrackingReport: function() {
      window._backend.sheetUIShowSidebar()
      window._frontend.close()
    },
    getAttachmentsLength: function() {
      const activeTemplate = this.$store.getters.getActiveTemplate
      const attachments = activeTemplate && activeTemplate.attachments
      let totalAttachmentsOnMerge = 0

      if (attachments) {
        for (const key in attachments) {
          if (Object.prototype.hasOwnProperty.call(attachments, key)) {
            totalAttachmentsOnMerge++
          }
        }
      }

      return totalAttachmentsOnMerge
    }
  },
  mounted: function() {
    const hasAttachments = this.getAttachmentsLength()

    window._backend.sheetUIIncreaseMergesCounter()

    /* eslint-disable @typescript-eslint/camelcase */

    window.gtag('event', 'sent_campaign', {
      event_category: 'engagement',
      event_label: hasAttachments ? 'with_attachments' : '',
      value: this.sheet.emailing.recipients
    })

    window.saasmetrics('event', {
      category: 'engagement',
      action: 'emails_sent',
      label: hasAttachments ? 'with_attachments' : '',
      value: this.sheet.emailing.recipients
    })
  }
})
