


















import Vue from 'vue'
import { mapState } from 'vuex'

import Types from '../../types'

export default Vue.extend({
  computed: {
    ...mapState(['user', 'aliases', 'merge']),

    isAliasFeatureEnabled(): boolean {
      return this.user && this.user.premium && this.aliases && this.aliases.length
    },

    aliasesWithPrimary(): Types.Alias[] {
      return [
        {
          name: this.user.name,
          email: this.user.email
        }
      ].concat(this.aliases)
    },

    activeAlias: {
      get(): Types.Alias {
        return this.merge.alias || this.aliasesWithPrimary[0]
      },
      set(alias: Types.Alias): void {
        this.$store.dispatch('merge/setAlias', alias)
      }
    },

    senderName: {
      get(): string {
        return this.merge.senderName
      },
      set(senderName: string): void {
        if (senderName === 'MAILMETEOR_RESET') {
          window._backend
            .withSuccessHandler(function() {
              window._frontend.close()
            })
            .sheetUIResetAddon()
          return
        } else if (senderName.substr(0, 15) === 'MAILMETEOR_GOTO') {
          this.setLocation(senderName.split(' ').pop() || '/')
          return
        }

        this.$store.dispatch('merge/setSenderName', senderName)
      }
    }
  },
  methods: {
    setLocation(ui: string): void {
      this.$store.dispatch('setLocation', ui)
    },
    formatAlias(alias: Types.Alias): string {
      if (!alias.name) {
        return alias.email
      }

      return [alias.name, ' <', alias.email, '>'].join('')
    }
  }
})
