
























































import Vue from 'vue'
import { mapState } from 'vuex'
import * as firebase from 'firebase/app'
import 'firebase/database'

import ModalFooterIcons from './FooterIcons.vue'
import ModalComposeEditor from './ComposeEditor.vue'
import Types, { Attachment } from '../../types'

export default Vue.extend({
  components: { ModalFooterIcons, ModalComposeEditor },
  data() {
    return {
      subject: '',
      initialHtmlBody: null,
      htmlBody: null as string | null,
      attachments: {} as { [key: string]: Types.AttachmentEvent },
      upgradeUI: false,
      window: 'base'
    }
  },
  computed: mapState(['merge']),
  mounted(): void {
    if (this.$store.state.location === '/templates/new') {
      this.$store.dispatch('templates/select', null)
      return
    }

    const template = this.$store.getters.getActiveTemplate

    // Fetch template information
    if (template && template.id) {
      this.subject = template.subject

      if (template.attachments) {
        for (const id in template.attachments) {
          this.$set(this.attachments, id, template.attachments[id])
        }
      }

      firebase
        .database()
        .ref('templates/' + template.id)
        .once('value', snap => {
          this.initialHtmlBody = snap.val().body
        })
    }
  },

  methods: {
    setLocation(ui: string): void {
      this.$store.dispatch('setLocation', ui)
    },
    cancel(): void {
      this.setLocation(':back')
    },

    /**
     * Handle content updated
     */

    onContentUpdated(value: string): void {
      this.htmlBody = value
    },

    /**
     * Handle attachment's events
     */

    onAttachmentCreated(attachment: Types.AttachmentEvent): void {
      this.$set(this.attachments, attachment.id, attachment)
    },

    onAttachmentUpdated(newAttachment: Types.AttachmentEvent): void {
      const attachments = this.attachments
      for (const id in attachments) {
        const attachment = attachments[id]

        if (newAttachment.id === attachment.id) {
          if (newAttachment.key !== 'uploaded') {
            this.$set(attachments[id], newAttachment.key, newAttachment.value)
          }
          break
        }
      }
    },

    onAttachmentRemoval(attachment: Types.AttachmentEvent): void {
      this.$delete(this.attachments, attachment.id)
    },

    onAttachmentNeedsUpgrade(): void {
      this.$store.dispatch('showSplashScreen', 'attachment')
    },

    /**
     * Handle saving template
     */

    saveTemplate(): void {
      if (!this.subject || this.subject == '') {
        this.$store.dispatch('showButterbar', "Can't save. The subject is empty.")
        return
      }

      const htmlBody = this.htmlBody ? this.htmlBody : ''
      const activeTemplate = this.$store.getters.getActiveTemplate
      const template = {
        id: activeTemplate ? activeTemplate.id : null,
        subject: this.subject,
        htmlBody: htmlBody,
        attachments: this.getAttachmentsCleaned(),
        updatedAt: new Date().getTime()
      }

      const length = JSON.stringify(template).length

      if (length > 170000) {
        this.$store.dispatch('showButterbar', "Can't save. Email is too long.")
        return
      }

      if (!template.id) {
        this.$store.dispatch('templates/create', template)
      } else {
        this.$store.dispatch('templates/update', template)
      }

      this.setLocation(':back')
    },

    getAttachmentsCleaned: function() {
      const attachments = {} as { [key: string]: Attachment }
      for (const id in this.attachments) {
        const attachment = this.attachments[id]

        if (!attachment.error && !attachment.progress) {
          attachments[id] = {
            id: attachment.id,
            name: attachment.name,
            size: attachment.size
          }
        }
      }

      return attachments
    }
  }
})
