

























































import Vue from 'vue'
import { mapState } from 'vuex'
import i18n from 'vue-i18n'
import Types from '../../types'

export default Vue.extend({
  computed: {
    ...mapState(['splash', 'user']),
    isActive(): boolean {
      return (this.splash && this.splash.active) || false
    },
    maxUserQuota(): number {
      const domain = this.user.domain
      const gmaildomains = ['gmail.com', 'googlemail.com']
      return gmaildomains.indexOf(domain) >= 0 ? 500 : 1500
    },
    heroBg(): { [key: string]: string } | string {
      return this.fromtheme.hero ? { 'background-image': `url(${this.fromtheme.hero})` } : ''
    },
    fromtheme(): {
      title: i18n.TranslateResult
      description: i18n.TranslateResult
      primary: { text: i18n.TranslateResult; url?: i18n.TranslateResult; action?: string }
      secondary: i18n.TranslateResult | { text: i18n.TranslateResult; action?: string }
      hint?: i18n.TranslateResult
      hero?: i18n.TranslateResult
    } {
      const maxUserQuota = this.maxUserQuota
      const splash = this.splash as Types.UISplashScreen

      switch (splash.theme) {
        case 'add-review':
          return {
            hero: this.$t('MODAL_SPLASH_ADD_REVIEW_HERO'),
            title: this.$t('MODAL_SPLASH_ADD_REVIEW_TITLE'),
            description: this.$t('MODAL_SPLASH_ADD_REVIEW_DESCRIPTION'),
            primary: {
              text: this.$t('MODAL_SPLASH_ADD_REVIEW_PRIMARY_TEXT'),
              url: this.$t('MODAL_SPLASH_ADD_REVIEW_PRIMARY_LINK')
            },
            secondary: this.$t('MODAL_SPLASH_ADD_REVIEW_SECONDARY_TEXT'),
            hint: this.$t('MODAL_SPLASH_ADD_REVIEW_HINT')
          }

        case 'alias':
          return {
            title: this.$t('MODAL_SPLASH_ALIAS_TITLE'),
            description: this.$t('MODAL_SPLASH_ALIAS_DESCRIPTION'),
            primary: {
              text: this.$t('MODAL_SPLASH_ALIAS_PRIMARY_TEXT'),
              url: this.$t('MODAL_SPLASH_ALIAS_PRIMARY_LINK')
            },
            secondary: this.$t('MODAL_SPLASH_ALIAS_SECONDARY_TEXT')
          }

        case 'attachment':
          return {
            title: this.$t('MODAL_SPLASH_ATTACHMENT_TITLE'),
            description: this.$t('MODAL_SPLASH_ATTACHMENT_DESCRIPTION'),
            primary: {
              text: this.$t('MODAL_SPLASH_ATTACHMENT_PRIMARY_TEXT'),
              url: this.$t('MODAL_SPLASH_ATTACHMENT_PRIMARY_LINK')
            },
            secondary: this.$t('MODAL_SPLASH_ATTACHMENT_SECONDARY_TEXT')
          }

        case 'discount':
          return {
            title: this.$t('MODAL_SPLASH_DISCOUNT_TITLE'),
            description: this.$t('MODAL_SPLASH_DISCOUNT_DESCRIPTION', { n: maxUserQuota }),
            primary: {
              text: this.$t('MODAL_SPLASH_DISCOUNT_PRIMARY_TEXT'),
              url: this.$t('MODAL_SPLASH_DISCOUNT_PRIMARY_LINK')
            },
            secondary: this.$t('MODAL_SPLASH_DISCOUNT_SECONDARY_TEXT')
          }

        case 'cancel':
          return {
            hero: this.$t('MODAL_SPLASH_CANCEL_HERO'),
            title: this.$t('MODAL_SPLASH_CANCEL_TITLE'),
            description: this.$t('MODAL_SPLASH_CANCEL_DESCRIPTION'),
            primary: {
              text: this.$t('MODAL_SPLASH_CANCEL_PRIMARY_TEXT'),
              action: ':close'
            },
            secondary: {
              text: this.$t('MODAL_SPLASH_CANCEL_SECONDARY_TEXT'),
              action: '/account/cancel'
            }
          }

        case 'upgrade':
        default:
          return {
            title: this.$t('MODAL_SPLASH_UPGRADE_TITLE'),
            description: this.$t('MODAL_SPLASH_UPGRADE_DESCRIPTION', { n: maxUserQuota }),
            primary: {
              text: this.$t('MODAL_SPLASH_UPGRADE_PRIMARY_TEXT'),
              url: this.$t('MODAL_SPLASH_UPGRADE_PRIMARY_LINK')
            },
            secondary: this.$t('MODAL_SPLASH_UPGRADE_SECONDARY_TEXT')
          }
      }
    }
  },

  methods: {
    close(): void {
      this.$store.dispatch('closeSplashScreen')
    },
    goToAction(type: 'primary' | 'secondary') {
      const theme = this.fromtheme
      const themeType = theme[type] && theme[type]
      const themeAction = themeType && typeof themeType !== 'string' && themeType.action

      this.close()

      if (typeof themeAction !== 'string' || themeAction === ':close') {
        return
      } else {
        this.$store.dispatch('setLocation', themeAction)
      }
    }
  }
})
