










































import Vue from 'vue'
import { mapState } from 'vuex'

export default Vue.extend({
  computed: {
    ...mapState(['templates', 'merge']),

    hasTemplates(): boolean {
      return this.templates.length > 0
    },

    activeTemplate: {
      get(): string {
        return this.$store.getters.getActiveTemplate || ''
      },
      set(template): void {
        this.$store.dispatch('templates/select', template)
      }
    }
  },
  methods: {
    setLocation(ui: string) {
      this.$store.dispatch('setLocation', ui)
    },
    createTemplate() {
      this.setLocation('/templates/new')
    },
    manageTemplates() {
      this.setLocation('/templates')
    }
  }
})
