import Vue from 'vue'
import { ActionTree, MutationTree } from 'vuex'
import Types from '../../types'

const state = [] as Types.Alias[]

const actions: ActionTree<Types.Alias[], Types.RootState> = {
  /**
   * Initializer
   */

  initialize(context, aliases: Types.Alias[]) {
    context.commit('localUpdateAll', aliases)
  },

  /**
   * Select a template
   */

  select(context, alias: Types.Alias) {
    context.dispatch('merge/setAlias', alias, { root: true })
  },

  /**
   * Update a template
   */

  update(context, payload: { oldAlias: Types.Alias; newAlias: Types.Alias }) {
    context.commit('localUpdate', payload)
    context.dispatch('saveAll')
  },

  /**
   * Save all templates both locally and in the database
   */

  saveAll() {
    const oldAliases = state
    const newAliases = []

    for (let i = 0, n = oldAliases.length; i < n; i++) {
      const alias = oldAliases[i]

      if (alias.email && alias.name) {
        newAliases.push({ email: alias.email, name: alias.name })
      }
    }

    // Save new aliases within user profile
    window._backend.sheetUISaveAliases(newAliases)
  },

  /**
   * Delete a template
   */

  remove(context, alias: Types.Alias) {
    context.commit('localRemove', alias)
    context.dispatch('saveAll')

    // If the active template is the one removed, unselect it
    context.dispatch('merge/unsetAlias', alias, { root: true })
  }
}

const mutations: MutationTree<Types.Alias[]> = {
  /**
   * Remove an alias locally (from the store)
   */

  localRemove: function(state, alias: Types.Alias) {
    for (let i = 0, n = state.length; i < n; i++) {
      if (state[i].name === alias.name && state[i].email === alias.email) {
        Vue.delete(state, i)
        return
      }
    }
  },

  /**
   * Update an alias locally (on the store)
   */

  localUpdate: function(state, payload: { oldAlias: Types.Alias; newAlias: Types.Alias }) {
    for (let i = 0, n = state.length; i < n; i++) {
      if (state[i].name === payload.oldAlias.name && state[i].email === payload.oldAlias.email) {
        Vue.set(state, i, payload.newAlias)
        return
      }
    }

    // If we couldn't find a previous alias in the state,
    // create a new one.
    state.push(payload.newAlias)
  },

  /**
   * Update all templates locally (within the store)
   */

  localUpdateAll(state, newAliases) {
    // Clear state
    for (let i = 0, n = state.length; i < n; i++) {
      Vue.delete(state, i)
    }

    // Assign new aliases to the state
    // Note: Object.assign is not supported by IE
    for (let i = 0, n = newAliases.length; i < n; i++) {
      Vue.set(state, i, newAliases[i])
    }
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations
}
