

































import Vue from 'vue'
import { mapState } from 'vuex'

export default Vue.extend({
  data: function() {
    return {
      timerObj: undefined as number | undefined,
      countdown: 5, // in seconds.
      sendingIntervalWithoutAttachments: 1.2, // in seconds.
      sendingIntervalWithAttachments: 3.6, // in seconds.
      totalEmailsSent: 0,
      sendingTipAlmostCompleted: false,
      til: '',
      facts: [
        'On July. 29, 1958, the NASA was created',
        "Halley's Comet was last seen from Earth in 1986",
        "Halley's Comet is expected to re-appear in mid-2061",
        'Geminids are a meteor shower peaking around December 6–14',
        'On Jan. 9, 1992, the first exoplanet was detected',
        'Valentina Tereshkova was the first woman in space',
        'There are 203 known moons in our Solar System',
        'The Sun weights ~99,9% of all mass in our Solar System',
        'The ISS has costed to date ~160 billion USD',
        'The Apollo Program has costed ~163 billion USD',
        'The Moon is on average 384,400 km away from Earth',
        'Earth formed around 4.54 billion years ago',
        'There are 8 "real planets" in our Solar System',
        'First animal in space was a dog named Laïka',
        'The closest star beyond our Sun is 4.22 light-years away',
        'The Sun is 8 light minutes away from Earth...',
        'Only 12 people have walked on the moon',
        'The 1st observation of gravitational waves was made on Sep. 14, 2015',
        'Sagittarius B2, a vast cloud in our Galaxy, smells like raspberries',
        "Saturn's rings will eventually fall into Saturn and disappear completely",
        'Astronauts witness 16 sunrises and 16 sunsets every day on the ISS'
      ]
    }
  },
  mounted(): void {
    this.timer()
  },
  beforeDestroy(): void {
    if (this.timerObj) {
      window.clearTimeout(this.timerObj)
    }
  },
  computed: {
    ...mapState(['merge', 'sheet', 'user']),
    totalEmailsToSend(): number {
      return this.user.quota.available > this.sheet.emailing.recipients ? this.sheet.emailing.recipients : this.user.quota.available
    },
    sendingInterval(): number {
      return this.getAttachmentsLength() > 0 ? this.sendingIntervalWithAttachments : this.sendingIntervalWithoutAttachments
    }
  },
  methods: {
    setLocation(ui: string) {
      this.$store.dispatch('setLocation', ui)
    },
    getAttachmentsLength: function() {
      const activeTemplate = this.$store.getters.getActiveTemplate
      const attachments = activeTemplate && activeTemplate.attachments
      let totalAttachmentsOnMerge = 0

      if (attachments) {
        for (const key in attachments) {
          if (Object.prototype.hasOwnProperty.call(attachments, key)) {
            totalAttachmentsOnMerge++
          }
        }
      }

      return totalAttachmentsOnMerge
    },

    /**
     * Handle the undo send
     * (with a few seconds delay)
     */

    timer: function() {
      if (this.countdown <= 0) {
        return this.startCampaign()
      }

      this.countdown--
      this.timerObj = window.setTimeout(this.timer, 1000)
    },

    /**
     * Actually start sending emails
     */

    startCampaign: function() {
      const progressbar = document.getElementById('sheetUISendingProgressmeter')
      if (!progressbar) {
        return
      }

      progressbar.style.animationDuration = Math.ceil(this.totalEmailsToSend * this.sendingInterval) + 's'

      const parentNode = progressbar.parentNode as HTMLDivElement
      if (parentNode) {
        parentNode.setAttribute('class', 'addon-progressbar animate')
      }

      this.sendEmails()
      this.emailsSentCounter()
      this.renderTil()
    },
    emailsSentCounter: function() {
      if (this.totalEmailsSent < this.totalEmailsToSend) {
        this.totalEmailsSent++
      }

      window.setTimeout(this.emailsSentCounter, this.sendingInterval * 1000)
    },

    /**
     * Handle the TIL displayed during the sending
     */

    renderTil: function() {
      if (this.sheet.stats.totalRows < 30) {
        return
      }

      if (this.totalEmailsSent > 3) {
        const progress = Math.round((this.totalEmailsSent / this.totalEmailsToSend) * 100)

        if (progress < 90) {
          const i = Math.floor(Math.random() * this.facts.length)
          this.til = this.facts[i]
        } else {
          this.til = this.$t('SHEET_SENDING_ALMOST_COMPLETE').toString()
          return // End the tip
        }
      }

      return window.setTimeout(this.renderTil, 8000)
    },

    /**
     * Actually send emails (in batch)
     */

    sendEmails: function(cid?: string, startAt?: number) {
      const merge = this.merge
      const sheet = this.sheet

      if (merge.template && merge.template.htmlBody) {
        merge.template.plainBody = this.html2text(merge.template.htmlBody)
      }

      window._backend.withSuccessHandler(this.sendEmailsSuccess).sheetUISendMails(merge, sheet, cid, startAt)
    },

    sendEmailsSuccess(data: { merge: { endRow: number; totalRows: number; cid: string } }): void {
      // Data shall be an object
      if (!data || typeof data !== 'object' || !data.merge) {
        throw new Error('Sending emails failed. Data returned is malformed')
      }

      if (data.merge.endRow === -1) {
        throw new Error('Sending emails failed. End row is -1')
      }

      // A campaign might contains several merge
      if (data.merge.endRow < data.merge.totalRows) {
        this.sendEmails(data.merge.cid, data.merge.endRow + 1)
        return
      }

      // Sending the campaign might be faster than the progress bar slider
      // If so, we wait until the progress counter is 100%
      if (this.totalEmailsSent < this.totalEmailsToSend) {
        window.setTimeout(() => {
          this.sendEmailsSuccess(data)
        }, 800)
        return
      }

      // When everything is green, switch UI
      this.setLocation('/sent')
    },

    html2text(html: string): string {
      const transformer = document.createElement('div')
      document.body.appendChild(transformer)

      transformer.innerHTML = html
      const text = transformer.innerText

      document.body.removeChild(transformer)

      return text
    }
  }
})
