import Vue from 'vue'
import { ActionTree, MutationTree } from 'vuex'
import Types from '../../types'
import * as firebase from 'firebase/app'
import 'firebase/database'

const state = [] as Types.Template[]

/**
 * Actions
 */

const actions: ActionTree<Types.Template[], Types.RootState> = {
  /**
   * Initializer
   */

  initialize(context) {
    const ref = firebase.database().ref('/users/' + context.rootState.user.uid + '/templates/')

    ref.on('child_added', snapshot => {
      const template = snapshot.val()

      context.commit('localAdd', {
        id: snapshot.key,
        subject: template.subject,
        updatedAt: template.updatedAt,
        attachments: template.attachments
      })
    })

    ref.on('child_changed', snapshot => {
      const newTemplateData = snapshot.val()

      context.commit('localUpdate', {
        id: snapshot.key,
        subject: newTemplateData.subject,
        updatedAt: newTemplateData.updatedAt,
        attachments: newTemplateData.attachments
      })
    })
  },

  /**
   * Create a new template
   */

  create(context, template: Types.Template) {
    // Create template within Firebase
    const templatesRef = firebase.database().ref('templates')
    const newTemplateRef = templatesRef.push()
    template.id = newTemplateRef.key || ''
    template.updatedAt = new Date().getTime()

    if (!template.id) {
      throw new Error('Unable to create template. ID has been auto-generated')
    }

    // Update the template
    context.dispatch('update', template)

    // PS: no need to add the template locally (to the store)
    // because it will be added through the campaigns listeners
    // (see the initializer).

    // Set this template as the active one
    context.dispatch('select', template)
  },

  /**
   * Update a template
   */

  update(context, template: Types.Template) {
    const ref = firebase.database().ref()
    const updates = {} as any /* eslint-disable-line @typescript-eslint/no-explicit-any */
    const uid = context.rootState.user.uid

    // Update the user's list of templates
    updates['users/' + uid + '/templates/' + template.id] = {
      subject: template.subject,
      updatedAt: template.updatedAt,
      attachments: template.attachments || null
    }

    // Update the list of all templates
    updates['templates/' + template.id] = { body: template.htmlBody, owner: uid }
    ref.update(updates)

    // Update the template locally (in the store)
    context.commit('localUpdate', template)
  },

  /**
   * Select a template for a merge
   */

  select(context, template: Types.Template) {
    context.dispatch('merge/setTemplate', template, { root: true })

    if (template && template.id && !template.htmlBody) {
      firebase
        .database()
        .ref('templates/' + template.id)
        .once('value', function(snapshot) {
          const value = snapshot.val()

          if (!value) {
            return
          }

          template.htmlBody = (value.body as string) || ''

          // Update the template locally (in the store)
          context.commit('localUpdate', template)
        })
    }
  },

  /**
   * Delete a template
   */

  remove(context, template: Types.Template) {
    // Remove from local store
    context.commit('localRemove', template)

    // Remove from database
    const db = firebase.database()
    const ref = db.ref('users/' + context.rootState.user.uid + '/templates/' + template.id)
    ref.set(null, error => {
      if (!error) {
        db.ref('templates/' + template.id).set(null)
      }
    })

    // If the active template is the one removed, unselect it
    context.dispatch('merge/unsetTemplate', template, { root: true })
  },

  /**
   * Edit a template
   */

  edit(context, template: Types.Template) {
    context.dispatch('select', template)
    context.dispatch('setLocation', '/templates/edit', { root: true })
  }
}

/**
 * Mutations
 */

const mutations: MutationTree<Types.Template[]> = {
  /**
   * Add a template locally (to the store)
   */

  localAdd(state, template: Types.Template) {
    const templates = state
    templates.push(template)

    state = templates.sort(function(a, b) {
      return b.updatedAt - a.updatedAt
    })
  },

  /**
   * Remove a template locally (from the store)
   */

  localRemove: function(state, template: Types.Template) {
    for (let i = 0, n = state.length; i < n; i++) {
      if (state[i].id == template.id) {
        Vue.delete(state, i)
        break
      }
    }
  },

  /**
   * Update a template locally (on the store)
   */

  localUpdate: function(state, template: Types.Template) {
    for (let i = 0, n = state.length; i < n; i++) {
      if (state[i].id === template.id) {
        Vue.set(state[i], 'subject', template.subject)
        Vue.set(state[i], 'updatedAt', template.updatedAt)
        Vue.set(state[i], 'attachments', template.attachments)

        if (template.htmlBody) {
          Vue.set(state[i], 'htmlBody', template.htmlBody || '')
        }

        break
      }
    }
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations
}
