










































import Vue from 'vue'
import { mapState } from 'vuex'

export default Vue.extend({
  data() {
    return {
      settings: false
    }
  },
  computed: {
    ...mapState(['user', 'location', 'campaigns']),
    isHome(): boolean {
      return this.location === '/sidebar'
    },
    isUserUpgradable(): boolean {
      if (this.user) {
        return !this.user.premium
      }

      return false
    }
  },
  methods: {
    openModal(): void {
      window._backend
        .withSuccessHandler(function() {
          window._frontend.close()
        })
        .sheetUIShowModal()
    },

    openModalAccount(): void {
      window._backend
        .withSuccessHandler(function() {
          window._frontend.close()
        })
        .sheetUIShowModalAccount()
    },

    goToOverview(): void {
      this.$store.dispatch('setLocation', '/sidebar')
    },
    toggleSettings(): void {
      if (!this.settings) {
        document.addEventListener('click', this.toggleSettings, true)
      } else {
        document.removeEventListener('click', this.toggleSettings, true)
      }

      this.settings = !this.settings
    }
  }
})
