



















































import Vue from 'vue'

export default Vue.extend({
  props: ['label', 'tooltip', 'total', 'active'],
  computed: {
    percent(): number {
      if (isNaN(this.total) || this.total < 1) {
        return 0
      } else if (isNaN(this.active) || this.active < 1) {
        return 0
      } else if (this.active > this.total) {
        return 100
      } // error
      else {
        return +((this.active / this.total) * 100).toFixed(2)
      }
    },
    progressBarWidth(): string {
      return 'width:' + this.percent + '%'
    }
  }
})
