import Vue from 'vue'
import { ActionTree, MutationTree } from 'vuex'
import * as firebase from 'firebase/app'
import 'firebase/database'
import Types from '../../types'

interface CampaignMinimal {
  cid: string
  column: number
  columnName: string
  columnHeading: string
}

interface CampaignEvent extends CampaignMinimal {
  eid: string | null
  status: 1 | 2
}

// Initialize Firebase
const eventConfig = window._app && window._app.firebase_events[0]
const eventDB = firebase.initializeApp(eventConfig, 'event')

// State
const state: Types.Campaign[] = []
let events: CampaignEvent[] = []

const actions: ActionTree<Types.Campaign[], Types.RootState> = {
  /**
   * Initializer
   */

  initialize(context, campaigns: CampaignMinimal[]) {
    context.dispatch('fetchAll', campaigns)
    context.dispatch('campaignEventsHandler')
  },

  /**
   * Fetch all campaigns details from the database
   * and save them locally (in the store)
   */

  fetchAll(context, campaigns: CampaignMinimal[]) {
    const db = firebase.database()

    for (let i = 0, n = campaigns.length; i < n; i++) {
      const campaign = campaigns[i]
      const cid = campaign.cid
      const column = {
        id: campaign.column,
        name: campaign.columnName,
        heading: campaign.columnHeading
      }

      const ref = db.ref('campaigns/' + cid)

      ref.on('value', snapshot => {
        const campaignData = snapshot.val()

        if (!campaignData || !campaignData.metadata) {
          return
        }

        const campaign = {
          cid: cid,
          name: campaignData.metadata.name,
          column: column.id,
          columnName: column.name,
          columnHeading: column.heading,
          createdAt: campaignData.metadata.createdAt,
          doNotTrack: campaignData.metadata.doNotTrack,
          metrics: {
            emails: campaignData.metrics.e || 0,
            opens: campaignData.metrics.o || 0,
            clicks: campaignData.metrics.c || 0
          }
        }

        context.commit('localUpdate', campaign)
        context.dispatch('campaignEventsListener', campaign)
      })
    }
  },

  /**
   * Watch a campaign's events
   */

  campaignEventsListener(context, campaign: Types.Campaign) {
    // Firebase events listener for clicks
    firebase
      .database(eventDB)
      .ref('/events/open/' + campaign.cid)
      .on('child_added', function(snapshot) {
        const eventCode = 1
        context.dispatch('campaignEventsStack', { cid: campaign.cid, column: campaign.column, eid: snapshot.key, status: eventCode })
      })

    // Firebase events listener for clicks
    firebase
      .database(eventDB)
      .ref('/events/click/' + campaign.cid)
      .on('child_added', function(snapshot) {
        const eventCode = 2
        context.dispatch('campaignEventsStack', { cid: campaign.cid, column: campaign.column, eid: snapshot.key, status: eventCode })
      })
  },

  /**
   * Stack all campaigns events
   */

  campaignEventsStack(context, event: CampaignEvent) {
    if (!event.eid) {
      return
    }

    events.push(event)
  },

  /**
   * Update a spreadsheet with campaigns events
   */

  campaignEventsHandler(context) {
    if (events.length) {
      window._backend.sheetUIUpdateSheetWithEvents(events)

      events = []
    }

    window.setTimeout(() => {
      context.dispatch('campaignEventsHandler')
    }, 2500)
  }
}

const mutations: MutationTree<Types.Campaign[]> = {
  /**
   * Update a template locally (to the store)
   */

  localUpdate(state, campaign: Types.Campaign) {
    const campaigns = state

    // Try to update the campaign (if already stored)
    for (let i = 0, n = campaigns.length; i < n; i++) {
      if (campaigns[i].cid === campaign.cid) {
        return Vue.set(state, i, campaign)
      }
    }

    // Otherwise push it
    campaigns.push(campaign)

    state = campaigns.sort((a, b) => {
      return b.column > a.column ? -1 : 1
    })
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations
}
