









































































import Vue from 'vue'
import ModalAliasesItem from './AliasesItem.vue'
import ModalFooterIcons from './FooterIcons.vue'
import { mapState } from 'vuex'

export default Vue.extend({
  components: { ModalAliasesItem, ModalFooterIcons },
  data: function() {
    return {
      createNew: false,
      emptyAlias: {
        name: '',
        email: '',
        edit: true
      }
    }
  },
  computed: {
    ...mapState(['aliases', 'user']),
    aliasesWithPrimary(): Types.Alias[] {
      return [
        {
          name: this.user.name,
          email: this.user.email
        }
      ].concat(this.aliases)
    }
  },
  methods: {
    cancel: function() {
      this.$store.dispatch('setLocation', '/')
    },
    createNewAlias: function() {
      this.createNew = true
    },
    onNewAliasSaved: function() {
      this.createNew = false
      this.emptyAlias = {
        name: '',
        email: '',
        edit: true
      }
    }
  }
})
