











































































import Vue from 'vue'
import { mapState } from 'vuex'
import SidebarNavigation from './Navigation.vue'
import SidebarTrackingSection from './TrackingSection.vue'
import SidebarTips from './Tips.vue'
import SidebarFooter from './Footer.vue'

export default Vue.extend({
  components: { SidebarNavigation, SidebarTrackingSection, SidebarTips, SidebarFooter },
  computed: {
    ...mapState(['campaigns', 'activeCampaign']),

    campaign: {
      get(): Types.Campaign | null {
        if (this.activeCampaign) {
          return this.activeCampaign
        }

        return this.hasCampaigns ? this.campaigns[this.campaigns.length - 1] : null
      },
      set(campaign: Types.Campaign): void {
        this.$store.dispatch('setActiveCampaign', campaign)
      }
    },

    hasCampaigns(): boolean {
      return this.campaigns.length > 0
    },

    activeCampaignId(): string | null {
      return this.campaign && this.campaign.cid
    },

    isFirstCampaign(): boolean {
      return this.campaigns[0].cid === this.activeCampaignId
    },

    isLastCampaign(): boolean {
      const n = this.campaigns.length
      return this.campaigns[n - 1].cid === this.activeCampaignId
    }
  },

  methods: {
    openModal(): void {
      window._backend
        .withSuccessHandler(function() {
          window._frontend.close()
        })
        .sheetUIShowModal()
    },

    sentDate(campaign: Types.Campaign): string {
      if (!campaign) {
        return ''
      }

      const dateObj = new Date(campaign.createdAt || '')

      if (typeof dateObj.toLocaleString !== 'function') {
        return dateObj.toString()
      }

      const date = dateObj.toLocaleString(undefined, { year: 'numeric', month: 'numeric', day: 'numeric' })
      const time = dateObj.toLocaleString(undefined, { hour: '2-digit', minute: '2-digit' })

      return date + ' at ' + time
    },

    /**
     * Handle navigation
     */

    goToPrevCampaign(): void {
      const activeCampaignId = this.activeCampaignId
      for (let i = 1, n = this.campaigns.length; i < n; i++) {
        if (this.campaigns[i].cid === activeCampaignId) {
          this.campaign = this.campaigns[i - 1]
          break
        }
      }
    },

    goToNextCampaign(): void {
      const activeCampaignId = this.activeCampaignId
      for (let i = 0, n = this.campaigns.length; i < n - 1; i++) {
        if (this.campaigns[i].cid === activeCampaignId) {
          this.campaign = this.campaigns[i + 1]
          break
        }
      }
    }
  }
})
