




























































































import Vue from 'vue'
import { mapState } from 'vuex'
import ModalFooterIcons from './FooterIcons.vue'
import Types from '../../types'

export default Vue.extend({
  components: { ModalFooterIcons },
  computed: mapState(['templates']),
  methods: {
    setLocation(ui: string) {
      this.$store.dispatch('setLocation', ui)
    },
    cancel: function() {
      this.setLocation('/')
    },
    createNew: function() {
      this.setLocation('/templates/new')
    },
    editTemplate: function(template: Types.Template) {
      this.$store.dispatch('templates/edit', template)
    },
    deleteTemplate: function(template: Types.Template) {
      this.$store.dispatch('templates/remove', template)
    },
    selectTemplate: function(template: Types.Template) {
      this.$store.dispatch('templates/select', template)
      this.setLocation('/')
    },
    lastUpdated: function(template: Types.Template) {
      return template.updatedAt ? new Date(template.updatedAt).toLocaleDateString('en-US', { month: 'short', day: 'numeric' }) : ''
    }
  }
})
