



















































































































































































import Vue from 'vue'
import { mapState } from 'vuex'
import ModalFooterIcons from './FooterIcons.vue'
import ModalPreviewContent from './PreviewContent.vue'

export default Vue.extend({
  components: {
    ModalFooterIcons,
    ModalPreviewContent
  },
  data() {
    return {
      startingSheetRow: 2, // Start at row #2 in Spreadsheet.
      currentSheetRow: 2,
      sheetHiddenRows: [],

      isFetching: true,
      isSending: false,

      uiSendingTestMailClassName: '',

      parsingErrors: [],
      showParsingErrors: false,

      to: '',
      bcc: '',
      cc: '',
      subject: '',
      body: ''
    }
  },
  mounted(): void {
    if (!this.activeTemplate) {
      this.$store.dispatch('showButterbar', this.$t('SHEET_PREVIEW_ERROR_UNKNOWN_TEMPLATE'))
      return
    }
    this.fetchHiddenRows()
  },
  computed: {
    ...mapState(['merge', 'user', 'sheet']),

    activeTemplate(): Types.Template | null {
      return this.$store.getters.getActiveTemplate
    },

    sender(): string {
      return this.merge.senderName ? this.merge.senderName + ' <' + this.merge.senderEmail + '>' : this.merge.senderEmail
    },

    isActionsDisabled(): boolean {
      if (this.isFetching) {
        return true
      } else if (this.isSending) {
        return true
      }

      return false
    },

    isQuotaExceeded(): boolean {
      return this.user.quota.available <= 0
    },

    maxEmailsToSend(): number {
      if (this.isQuotaExceeded) {
        return this.sheet.emailing.recipients
      }

      return this.user.quota.available > this.sheet.emailing.recipients ? this.sheet.emailing.recipients : this.user.quota.available
    }
  },
  methods: {
    setLocation(ui: string): void {
      this.$store.dispatch('setLocation', ui)
    },

    editTemplate() {
      this.setLocation('/templates/edit')
    },

    /**
     * Fetch hidden rows
     */

    fetchHiddenRows(): void {
      window._backend.withSuccessHandler(this.fetchHiddenRowsSuccess).sheetUIDataHiddenRow(this.sheet.activeSheet)
    },

    // eslint-disable-next-line
    fetchHiddenRowsSuccess(json: any) {
      this.sheetHiddenRows = json.sheetHiddenRows
      this.togglePreview(0)
    },

    /**
     * Fetch preview
     */

    fetchPreview(rowNumber: number): void {
      this.currentSheetRow = rowNumber
      this.isFetching = true

      if (!this.$store.getters.getActiveTemplate) {
        this.isFetching = false
        return
      }

      window._backend.withSuccessHandler(this.fetchPreviewSuccess).sheetUIPreviewTemplate(this.merge, this.sheet, this.currentSheetRow)
    },

    // eslint-disable-next-line
    fetchPreviewSuccess(json: any): void {
      this.isFetching = false

      // Saving errors response (if any)
      this.parsingErrors = json['errors'] ? json['errors'] : ['{{ lol }} is not defined']

      // Retrieving preview
      const preview = json.preview

      function _get(key: number | string) {
        return preview[key] ? preview[key].toString() : null
      }

      this.to = _get('to')
      this.cc = _get('cc')
      this.bcc = _get('bcc')

      this.subject = _get('subject')

      const body = typeof _get('htmlBody') === 'string' ? _get('htmlBody') : _get('plainBody')
      this.body = typeof body === 'string' ? body : ''
    },

    /**
     * Parsing error splash screen
     */

    showParsingErrorSplash() {
      this.showParsingErrors = true
    },

    hideParsingErrorSplash() {
      this.showParsingErrors = false
    },

    /**
     * Send test email
     */

    sendTestEmail() {
      this.isSending = true
      this.uiSendingTestMailClassName = 'addon-button-progress-wrapper animate'

      window._backend.withSuccessHandler(this.sendTestEmailSuccess).sheetUISendTestMail(this.merge, this.sheet, this.currentSheetRow)
    },

    sendTestEmailSuccess() {
      this.isSending = false
      this.uiSendingTestMailClassName = ''

      window.gtag('event', 'sent_test_email', {
        /* eslint-disable-next-line */
        event_category: 'engagement',
        value: this.sheet.emailing.recipients
      })

      window.saasmetrics('event', {
        category: 'engagement',
        action: 'test_email_sent'
      })
    },

    /**
     * Toggle preview
     */

    togglePreview(increment: number, handleOutOfRange?: boolean): void {
      if (isNaN(increment)) {
        throw new Error('Unable to toggle preview, variable i is malformed.')
      }

      const rowNumber = this.currentSheetRow + increment

      // Let's start by validating that
      // the row number is not out of range
      if (rowNumber > this.sheet.stats.lastRow || rowNumber < this.startingSheetRow) {
        // First time we run this command
        // or we explicitly said to not handle out of range.
        if (increment === 0 || handleOutOfRange) {
          return // stop the function
        }
        // Row number is greater than total rows
        else if (rowNumber > this.sheet.stats.lastRow) {
          this.currentSheetRow = this.startingSheetRow + increment * -1 // we go back to 1
          return this.togglePreview(increment)
        }
        // Row number is under starting sheet row (row number #1)
        else if (rowNumber < this.startingSheetRow) {
          this.currentSheetRow = this.sheet.stats.lastRow + increment * -1 // or to the latest row
          return this.togglePreview(increment)
        }
      }

      // If not out of range
      // validate that the row is not hidden
      const hiddenRows = this.sheetHiddenRows as any[]
      const isRowHidden = hiddenRows.indexOf(rowNumber)

      if (isRowHidden == -1) {
        // When the computed row is not hidden,
        // fetch a preview of this row.
        return this.fetchPreview(rowNumber)
      }

      // Otherwise, if row is hidden,
      // continue by incrementing
      this.currentSheetRow = rowNumber
      const newIncrement = increment === 0 ? 1 : increment
      const newHandleOutOfRange = increment === 0 || handleOutOfRange
      return this.togglePreview(newIncrement, newHandleOutOfRange)
    }
  }
})
